.center {
  position: absolute;
  top: 25%;
  right: 50%;
  background-color: azure;
  border-radius: 15px;
}
.middiv{
  display: flex;
  justify-content: center;
  padding: 25px 30px;
}
.h1div{
  text-align: center;
  color: blueviolet;
}
.inputdiv{
  display: flex;
  gap: 20px;
  padding: 20px;
  justify-content: center;
}
.displayinput{
  justify-content: center;
    display: flex;
}
.styleli {
  list-style-type: none;
  padding: 0;
}

.disflex {
  display: flex;
  gap: 33px;
  justify-content: space-between;
}

.btndelete {
  border-radius: 18px;
  background: cornsilk;
  border: none;
}
.livesortedata{
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}
.livesortedata h1{
  border: 1px solid;
  padding: 4px;
  border-radius: 10px;
  color: yellowgreen;

}
.btnall{
  border-radius: 18px;
  background: #ffbbbb;
  border: none;
}
button.btnall:hover {
  background: violet;
}
.bordertata {
  border: 1px solid lightgray;
  padding: 10px;
  border-radius: 15px;
}

button.btndelete:hover {
  background: violet;
}
.butflex{
    display: flex;
    justify-content: space-between;
    gap: 24px;
    align-items: center;
    margin-top: 30px;
}
.butflex button{
  border: none;
  background-color: antiquewhite;
  padding: 10px;
  border-radius: 10px;
}

.butflex button:hover {
  background-color: #cac5cf;;
}
.tata{
  background-color: #cac5cf !important;
}