.flexdata {
    display: flex;
    justify-content: space-around;
    padding: 10px 10px;
    background-color: #eae0cf;
}

.backgry {
    background-color: var(--app-drawer-scrim-background, rgba(0, 0, 0, 0.5));
    height: 100%;
    width: 100%;
    display: none;
    position: absolute;
    opacity: 0.6;
    top: 0;
}

.bringdown {
    display: none;
}

.flexdata h3 {
    font-size: 33px;
    font-style: normal;
}

.listitem {
    display: flex;
    gap: 47px;
    list-style: none;
    margin-top: 6px;
    padding: 0;
}

.tatatata div {
    display: none;
    width: 35px;
    height: 5px;
    background-color: black;
    margin: 6px 0;
}

.serchform {
    display: flex;
    gap: 10px;
}


/*  */


.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    background-color: #071c2e;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
}

.sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
}

.sidenav .closebtn {
    position: absolute;
    top: 0;
    color: white;
    padding: 15px 27px;

}

.sidenav a:hover {
    background: #0e3f70;
    color: white !important;
    font-size: 30px;
    border-radius: 10px;
}

.mobilediv {
    list-style: none;
    color: #ffe5e5;
}

.maindivclass {
    background-color: #eae0cf;
}

/*  */

@media(min-width : 320px) and (max-width : 575.99px) {
    .listitem {
        display: none;
    }

    .tatatata div {
        display: block;
    }

    .flexdataflexdata {
        display: none;
    }

    .bringdown {
        display: block;
    }

    .flexdata {
        padding: 1px 11px;
    }

    .serchform {
        align-items: center;
        padding: 0;
    }
}


@media(min-width : 576px) and (max-width : 767.99px) {
    .listitem {
        display: none;
    }

    .tatatata div {
        display: block;
    }

}

@media(min-width : 768px) and (max-width : 991.99px) {
    .listitem {
        display: none;
    }

    .tatatata div {
        display: block;
    }


}